<template>
  <div id="exhibition-default">
    <div class="left_block">
      <!--  -->
      <div class="box1">
        <div class="curr_position">
          
          <a @click="selectItem('/home')"></a>
          <span>></span>
          <a @click="selectItem('/exhibition')"></a>
        </div>
      </div>
      <!--  -->
      <div class="box2">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;"></a>
          </li>
          <span class="more" @click="selectItem('')">-></span>
        </ul>
        <div style="height:170px;width:100%;">
          <keep-alive>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;"></a>
          </li>
          <span class="more" @click="selectItem('')">-></span>
        </ul>
        <div style="width:100%;">
          <keep-alive>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;"></a>
          </li>
          <span class="more" @click="selectItem()">-></span>
        </ul>
        <div style="width:100%;">
          <keep-alive>
          </keep-alive>
        </div>
      </div>
    </div>
    <div class="right_block">
      <!-- PPT   -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active"></a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === ''" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[6]"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === ''" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[7]"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active"></a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NormalList from "@/components/common/normal-list";

export default {
  name: "exhibition-default",
  data() {
    return {
      currentTab: [
        "pb-list",
        "pb-list",
        "pb-list2",
        "pb-list",
        "pb-list3",
        "pen-list",
        "",
        ""
      ], //
      tabs: [
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: true
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "normal-list", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "normal-list", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: true
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: false
        }
      ],
      links: [
        // {
        //   name: "",
        //   belong: "", //
        //   path: "/talent/exhibition-list" //
        // },
        // {
        //   name: "",
        //   belong: "", //
        //   path: "/talent/exhibition-list" //
        // }
      ]
    };
  },
  methods: {
    selectItem(path, data) {
      let item = JSON.stringify(data);
      this.$emit("select", path, item);
    }, //itemselect
    // routerLink(path) {
    //   this.$router.push(path); //
    // },
    tabLink(index, tocpn) {
      //
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //methodsdata
        }
      }
      var t = 6;
      if (this.tabs[index].belong == "") t = 6;
      else if (this.tabs[index].belong == "") t = 7;

      this.currentTab[t] = this.tabs[index].tocpn;
      //
    }
  },
  components: { NormalList }
};
</script>

<style lang="scss" scoped>
@mixin left {
  margin: 5px auto;
  width: 795px;
  float: left;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin right {
  margin: 5px auto;
  width: 390px;
  float: right;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin i-box {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
}
.active {
  color: #ed1c22 !important;
  border-bottom: 2px solid #ed1c22;
  padding-bottom: 4px;
}
#exhibition-default {
  margin: 5px auto;
  width: 1200px;
  overflow: hidden;
}
.left_block {
  @include left;
}
.right_block {
  @include right;
}
.box1,
.box2,
.box3 {
  @include left;
  @include i-box;
}
.box1 {
  & {
    color: #404040;
    font-weight: 500;
    padding: 0 15px;
    overflow: hidden;
  }
  .curr_position {
    & {
      margin-top: 14px;
      margin-bottom: 15px;
    }
    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
.box4 {
  @include right;
  @include i-box;
}
.title {
  margin-bottom: 12px;
  margin-top: 2px;
  width: 100%;
  height: 28px;
  line-height: 28px;
}
.title li {
  float: left;
}
.more {
  & {
    color: #666;
    font-size: 14px;
    float: right;
    padding: 2px 10px 0 2px;
    cursor: pointer;
  }
  &:hover {
    color: #ed1c22;
  }
}

.list_title a {
  color: #7b7b7b;
  cursor: default;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #ed1c22;
  border-bottom: 2px solid #ed1c22;
  cursor: default;
  padding-bottom: 4px;
}
</style>



