<template>
  <div id="talent-detail">
    <div class="left_block">
      <!--  -->
      <div class="box1">
        <div class="curr_position">
          
          <a @click="selectItem('/home')"></a>
          <span>></span>
          <a @click="selectItem(path[0])">{{init_data.belong}}</a>
          <span>></span>
          <a @click="selectItem(path[1],init_data)">{{init_data.name}}</a>
          <span>></span>
          <a>{{init_data.idName}}</a>
        </div>
      </div>
      <!--  -->
      <div class="box3">
        <div class="content_title">{{init_data.idName}}</div>
        <ul class="content_list">
          <div class="content" v-html="content"></div>
        </ul>
      </div>
    </div>
    <div class="right_block">
      <!--  -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active"></a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === ''" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[6]"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === ''" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[7]"></pb-list>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NormalList from "@/components/common/normal-list";

export default {
  name: "talent-detail",
  data() {
    return {
      currentTab: [
        "pb-list",
        "pb-list",
        "pb-list2",
        "pb-list",
        "pb-list3",
        "pen-list",
        "",
        ""
      ], //
      tabs: [
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: true
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "normal-list", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "normal-list", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: true
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: false
        }
      ],
      init_data: {
        idName: "",
        name: "",
        belong: "",
        catalog: ""
      },
      path: ["", ""],
      content: "",
      title: ""
    };
  },

  methods: {
    init() {
      let id = this.$route.query.id;
      console.log(this.$route.query.data);
      let data = JSON.parse(this.$route.query.data); //
      console.log(data);
      this.init_data.name = data.list;
      this.init_data.belong = data.name;
      let url;
      if (this.init_data.name == "") {
        // this.items = this.items.slice(0, 1);
        // this.path[1] = "/association/vip-list";
        // this.path[0] = "/association";
        // this.init_data.catalog = "";
        // let requestPath = data.requestPath;
        // url = this.HOST + requestPath;
        // this.axios({
        //   method: "post",
        //   url: url,
        //   data: {
        //     pageNum: 0
        //   }
        // })
        //   .then(response => {
        //     this.items = response.data.message.list;
        //   })
        //   .catch(response => {
        //     console.log(response);
        //   });
      }
      if (this.init_data.name == "") {
        this.path[1] = "/talent/talent-list";
        this.path[0] = "/talent";
        let requestPath = "/expert/findExpertInf";
        url = this.HOST + requestPath;
        this.axios
          .get(url, {
            params: {
              data: id
            }
          })
          .then(response => {
            this.init_data.idName = response.data.message.name;
            this.content = response.data.message.introduction;
          })
          .catch(response => {
            console.log(response);
          });
      }
      if (this.init_data.name == "") {
        this.path[1] = "/talent/talent-list";
        this.path[0] = "/talent";
        let requestPath = "/expert/findUniversityInf";
        url = this.HOST + requestPath;
        this.axios
          .get(url, {
            params: {
              data: id
            }
          })
          .then(response => {
            this.init_data.idName = response.data.message.name;
            this.content = response.data.message.introduction;
            this.title = response.data.message.name;
          })
          .catch(response => {
            console.log(response);
          });
      }
    },
    selectItem(path, item) {
      this.$emit("select", path, item);
    }, //itemselect
    // routerLink(path){
    //   this.$router.push(path);//
    // },
    tabLink(index, tocpn) {
      //
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //methodsdata
        }
      }
      if (tocpn != null) {
        var t = 6;
        if (this.tabs[index].belong == "") t = 6;
        else if (this.tabs[index].belong == "") t = 7;

        this.currentTab[t] = this.tabs[index].tocpn;
        //
      }
    }
  },
  components: {
    NormalList
  },
  activated() {
    this.init();
  } //
};
</script>

<style lang="scss" scoped>
@mixin left {
  margin: 5px auto;
  width: 795px;
  float: left;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin right {
  margin: 5px auto;
  width: 390px;
  float: right;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin i-box {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
}
.active {
  color: #ed1c22 !important;
  border-bottom: 2px solid #ed1c22;
  padding-bottom: 4px;
}
.active2 {
  & {
    box-sizing: border-box;
    color: #535353;
    font-weight: 600;
  }
  &::after {
    content: "";
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 4px;
    border-bottom: #ec252c 3px solid;
    z-index: 99;
  }
}
#talent-detail {
  margin: 5px auto;
  width: 1200px;
  overflow: hidden;
}
.left_block {
  @include left;
}
.right_block {
  @include right;
}
.box1,
.box2,
.box3 {
  @include left;
  @include i-box;
}
.box1 {
  & {
    color: #404040;
    font-weight: 500;
    padding: 0 15px;
    overflow: hidden;
  }
  .curr_position {
    & {
      margin-top: 14px;
      margin-bottom: 15px;
    }
    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
.box3 {
  color: #404040;
  padding: 20px 45px;
}
.content_title {
  text-align: center;
  color: #272222;
  font-size: 25px;
  font-weight: 500;
}
.content_list {
  color: #616161;
  margin: 10px 0;
  padding: 5px 20px;
}
.content_list li {
  list-style: disc;
  margin: 5px 0;
  cursor: pointer;
}
.content_list li:hover {
  color: #232424;
  font-size: 18px;
}
.box4 {
  @include right;
  @include i-box;
}
.title {
  margin-bottom: 12px;
  margin-top: 2px;
  width: 100%;
  height: 28px;
  line-height: 28px;
}
.title li {
  float: left;
}
.list_title a {
  color: #7b7b7b;
  cursor: default;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #ed1c22;
  border-bottom: 2px solid #ed1c22;
  cursor: default;
  padding-bottom: 4px;
}

//
/deep/ .news_right {
  width: 100% !important;
  position: relative;
  border: none !important;
}
/deep/ .xxy_dbt6 {
  display: none !important;
  font-size: 24px;
  color: #0071cf;
  display: block;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 10px;
}
/deep/ .xxy_ht2 {
  color: #000000;
  font-size: 14px;
  height: 40px;
  display: block;
  background: #f6f6f6;
  text-align: center;
  line-height: 40px;
  margin: 15px 15px;
}
/deep/ .xxy_ht2 span {
  padding-left: 5px;
  padding-right: 6px;
}
/deep/ #maincontent {
  min-height: 260px;
  margin-top: 15px;
  font-size: 16px;
  color: #2b2b2b;
}
/deep/ .xxnr {
  display: block;
  line-height: 26px;
  font-size: 14px;
  color: #5b5b5b;
  margin: 10px 5px;
}
/deep/ #maincontent p {
  text-align: center;
  line-height: 150%;
  text-indent: 2em;
  margin: 14px 0;
}
</style>


