  float: left;
  @include border;
}
.box3-right {
  width: 795px;
  float: right;
  @include border;
  @include i-box;
}

.fa-title {
  // margin-top: 2px;
  vertical-align: middle;
  font-size: 1.5em;
  color: #333;
}
.title {
  margin-bottom: 12px;
  margin-top: 2px;
  width: 100%;
  height: 28px;
  line-height: 28px;
}
.title li {
  float: left;
}
.menu_title {
  float: left;
  display: inline-block;
  padding: 0 10px;
  margin-right: 40px;
  cursor: default;
}
.menu_title span {
  font-size: 1em;
  font-weight: 500;
  color: #404040;
  line-height: 25px;
  vertical-align: middle;
}
.list_title a {
  cursor: pointer;
  color: #7b7b7b;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #ed1c22;
  border-bottom: 2px solid #ed1c22;
  padding-bottom: 4px;
}
</style>




