<template>
  <div id="association-introduce">
    <div class="img">
      <img src="../../../static/PICS/association.png" alt="">
    </div>
    <div class="content">
      <div class="name"><span></span></div>
      <div class="time"><span></span>2015516</div>
      <div class="introduction"><span></span><br><span class="more"></span></div>
      <div class="boss">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "association-introduce",
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#association-introduce {
  @include max;
  // border: 1px solid rgba(204, 204, 204, 0.575);
}
.img {
  & {
    float: left;
    height: 100%;
    display: inline-block;
    margin: 0 30px;
  }
  img {
    margin-top: 15px;
    width: 165px;
    height: 220px;
  }
}
.content {
  & {
    overflow: hidden;
    color: #3f3f3f;
    height: 100%;
    margin: 0 5px;
    box-sizing: border-box;
    line-height: 2;
    width: 500px;
    display: inline-block;
  }
  div {
    span {
      color: #ed1c22;
    }
  }
}
.introduction {
  & {
  }
  span {
  }
  .more {
    cursor: pointer;
  }
  .more:hover {
    color: #ce2126;
  }
}
</style>



