<template>
  <div id="x_Text">

    <!-- <m-header></m-header> -->

    <div v-html="this.content"></div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      Text:
        "                                   "
    };
  },
  props:{
    content:{
      
      required:true
    }
  }
};
</script>

<style lang="scss" scoped>
#x_text {
  width: 100%;
  text-indent: 2em;
  height: 100%;
}
</style>




